@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  background-color: #121212;
  color: #ffffff;
  overflow-x: hidden;
  font-family: "Montserrat", sans-serif;
}

a {
  text-decoration: none;
  color: inherit;
}

.row {
  padding: 1rem;
}
.row__images {
  display: flex;
  overflow-y: hidden;
  overflow-x: scroll;
  padding: 1rem;
}

.row__images::-webkit-scrollbar {
  display: none;
}

.row__image {
  max-width: 300px;
  max-height: 300px;
  object-fit: contain;
  margin: 0 0.2rem;
  transition: all 0.4s ease-in-out;
  -webkit-transition: all 0.4s ease-out;
  cursor: pointer;
}

.row__image:hover {
  transform: scale(1.04);
}
.row__imageHot:hover {
  transform: scale(1.07);
}
.row__images > div {
  position: relative;
}

.row__ratings {
  position: absolute;
  top: 1rem;
  right: 1rem;
  padding: 0.5rem 0.6rem;
  border-radius: 50%;
  color: #ffffff;
  border: 1px solid slateblue;
  text-align: center;
}

.row__ratings p {
  width: 25px;
}

.hide {
  display: none;
}

.hero {
  min-height: 70vh;
  display: flex;
  align-items: center;
  position: relative;
  padding: 2rem;
}

.hero__info h1 {
  margin-bottom: 1rem;
}

.hero__overlay {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 20%;
  background: linear-gradient(
    to bottom,
    transparent,
    rgba(0, 0, 0, 0.76),
    #121212de
  );
  z-index: 1;
}

.hero__overview {
  width: 50%;
}
.hero__button {
  padding: 1rem;
  text-transform: uppercase;
  border: 1px solid #ffffff;
  background-color: transparent;
  cursor: pointer;
  color: #ffffff;
  letter-spacing: 0.1rem;
  margin: 2rem 0;
  transition: all 0.3s ease-in;
  -webkit-transition: all 0.3s ease-in;
}

.hero__button:hover {
  background-color: steelblue;
}

@media (max-width: 900px) {
  .hero__overview {
    width: 70%;
  }
}

@media (max-width: 475px) {
  .hero__overview {
    width: 90%;
    font-size: 12px;
  }
  .hero__button {
    font-weight: 600;
  }
}

footer {
  background-color: steelblue;
  padding: 1rem;
  letter-spacing: 0.2rem;
  color: #121212;
  text-align: center;
  margin-top: 2rem;
}

@media (max-width: 475px) {
  footer {
    letter-spacing: 0.1rem;
    font-size: 14px;
  }
}

@media (max-width: 325px) {
  footer {
    font-size: 12px;
    padding: 1rem 0.5rem;
  }
}

